const { get, controller } = sword.Decorator;
@controller('/')
export default class Home extends sword.Controller {
    @get('/')
    async index() {
        this.body = `
            <!DOCTYPE html>
            <html lang="zh-cn">
            <head>
                <meta charset="utf-8">
                <meta name="viewport" content="width=device-width, initial-scale=1.0">
                <meta http-equiv="X-UA-Compatible" content="ie=edge">
                <link rel="shortcut icon" type="image/x-icon" href=${sword.assets.getUrl(
                    'favicon.ico'
                )} rel="external nofollow"/>                
                <link rel="stylesheet/less" type="text/css" href="${sword.assets.getUrl('theme.less')}" /> 
                <title>&lrm;</title>
                ${sword.assets.getCss('app')}
                <script>
                    window.SwordInfo = {
                        port: ${sword.config('port')}
                    }
                </script>
            </head>
            <body>
                <div id="app-root"></div>
                ${sword.assets.getJs('app')}
                ${sword.assets.getJs('example', '0.0.1')}
            </body>
            </html>
        `;
    }
}
